.card-developer{
  background: #DCEEF2;
  border-radius: 30px;
  width: 40%;
  margin-bottom: 2rem;
}
.avatar{
  width: 6rem;
  height: 6rem;
  overflow: hidden;
}
.container-img-developer > img, .container-img-developer > source {
  object-fit: cover;
  object-position: 0 10%;
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.icons-developer > a{
  color: #000;
  transition: all .2s ease-in-out;
}
.icons-developer > a:hover{
  color: #004f74;
}
@media screen and (max-width: 900px){ .card-developer{width: 45%;} }
@media screen and (max-width: 810px){ .card-developer{width: 75%;} }
@media screen and (max-width: 700px){ .card-developer{width: 90%;} }
@media screen and (max-width: 500px) {
  .container-card-developer{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .icons-developer {
    display: flex;
    justify-content: center;
  }
  .avatar{
    display: flex;
    justify-content: center;
  }
}